/**
 * Basic styles
 */
:root {
  box-sizing: border-box;
  text-size-adjust: none;
  font-family: sans-serif;
  font-size: 10px;
  direction: ltr;
  text-align: center;
  overflow-x: hidden !important;
}
:root *,
:root *::before,
:root *::after, :root::before, :root::after {
  box-sizing: inherit;
}

body {
  word-wrap: break-word;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 160%;
  text-align: initial;
  padding-left: 20px;
  padding-right: 20px;
  overflow-x: hidden !important;
  background-color: #fff;
  color: #333;
}

/** Rebuild normalized styles */
fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

figure {
  margin: 0;
}

/** What Input */
[data-whatintent=mouse] *:focus,
[data-whatintent=touch] *:focus {
  outline: none;
}

/**
 * Responsive embeds
 */
img,
iframe,
object,
embed,
video,
audio,
iframe[style],
object[style],
embed[style] {
  max-width: 100%;
}

img {
  height: auto;
}

/**
 * Predefined Classes
 */
.screen-reader-text {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  padding: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  margin: -1px;
  overflow: hidden;
  word-wrap: normal;
}
.screen-reader-text:focus {
  position: static;
  width: auto;
  height: auto;
  clip: auto !important;
  clip-path: none;
  margin: auto;
}

.self-clear::after {
  content: "";
  display: table;
  clear: both;
}

.p {
  margin-top: 20px;
  margin-bottom: 20px;
}
.p:first-child {
  margin-top: 0;
}
.p:last-child {
  margin-bottom: 0;
}

.full-width {
  margin-right: -9999px;
  margin-left: -9999px;
  padding-right: 9999px;
  padding-left: 9999px;
}

/**
 * Grid
 */
.grid {
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  margin: -10px;
}
.grid *,
.grid *::before,
.grid *::after, .grid::before, .grid::after {
