/**
 * Hyperlinks
 */
a {
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}

/**
 * Lists
 */
ul,
ol {
  margin-top: 20px;
  margin-bottom: 20px;
  list-style-image: none;
  margin-left: 30px;
  padding-left: 0;
}
ul:first-child,
ol:first-child {
  margin-top: 0;
}
ul:last-child,
ol:last-child {
  margin-bottom: 0;
}
ul ul,
ul ol,
ol ul,
ol ol {
  margin-top: 5px;
  margin-bottom: 5px;
}
ul ul:first-child,
ul ol:first-child,
ol ul:first-child,
ol ol:first-child {
  margin-top: 0;
}
ul ul:last-child,
ul ol:last-child,
ol ul:last-child,
ol ol:last-child {
  margin-bottom: 0;
}

ul {
  list-style-type: square;
}
ul ul {
  list-style-type: circle;
}

ol {
  list-style-type: decimal;
}
ol ol {
  list-style: upper-alpha;
}
ol ol ol {
  list-style: upper-roman;
}
ol ol ol ol {
  list-style: lower-alpha;
}
ol ol ol ol ol {
  list-style: lower-roman;
}

li {
  margin-top: 5px;
  margin-bottom: 5px;
  display: list-item;
}
li:first-child {
  margin-top: 0;
}
li:last-child {
  margin-bottom: 0;
}

/** Definition Lists */
dl {
  margin-right: 20px;
  margin-left: 20px;
}

dt {
  font-weight: bold;
}

dd {
  margin-top: 20px;
  margin-bottom: 20px;
  margin-right: 0;
  margin-left: 0;
}
dd:first-child {
  margin-top: 0;
}
dd:last-child {
  margin-bottom: 0;
}

/**
 * Typography
 */
p {
