  margin-bottom: 20px;
}
p:first-child {
  margin-top: 0;
}
p:last-child {
  margin-bottom: 0;
}
p:empty {
  display: none;
}

strong {
  font-weight: bold;
}

em,
i,
q,
cite,
var {
  font-style: italic;
}

q {
  font-size: 1.1em;
}

del {
  text-decoration: line-through;
}

cite,
pre,
code,
tt {
  font-size: 0.9em;
}

pre,
code,
tt {
  font-family: monaco, Consolas, "Lucida Console", monospace;
  padding: 2px 4px;
  background-color: #f4f4f4;
}

pre {
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 3px;
  padding: 10px;
  border: 1px solid #ddd;
  overflow: auto;
  line-height: 160%;
}
pre:first-child {
  margin-top: 0;
}
pre:last-child {
  margin-bottom: 0;
}

cite {
  color: #777;
}

abbr,
acronym {
  border: 0;
  cursor: help;
}

ins {
  text-decoration: none;
}

sup,
sub {
  position: relative;
  height: 0;
  line-height: 1px;
  vertical-align: baseline;
}

sup {
  bottom: 1ex;
}

sub {
  top: 0.5ex;
}

hr {
  margin-top: 20px;
  margin-bottom: 20px;
  border: 0;
  border-top: 1px solid #ddd;
}
hr:first-child {
  margin-top: 0;
}
hr:last-child {
  margin-bottom: 0;
}

blockquote {
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 1.1em;
  font-style: italic;
  line-height: 160%;
  margin-right: 0;
  margin-left: 10px;
  padding-left: 10px;
  border-left: 3px solid #ddd;
}
blockquote:first-child {
  margin-top: 0;
}
blockquote:last-child {
  margin-bottom: 0;
}
blockquote p {
  margin-top: 10px;
  margin-bottom: 10px;
}
blockquote p:first-child {
  margin-top: 0;
}
blockquote p:last-child {
  margin-bottom: 0;
}
blockquote cite {
  display: block;
  margin-top: 0.6em;
}

