/**
 * Forms
 */
button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
}

form {
  margin-top: 20px;
  margin-bottom: 20px;
}
form:first-child {
  margin-top: 0;
}
form:last-child {
  margin-bottom: 0;
}

*:disabled {
  opacity: 0.7 !important;
  cursor: not-allowed !important;
}

input,
textarea,
select,
button,
label {
  display: inline-block;
  zoom: 1;
  *display: inline;
  vertical-align: top;
  width: 100%;
}

input,
textarea,
select,
button {
  border-radius: 3px;
  border: 1px solid #ddd;
  padding: 5px 7px;
  margin-bottom: 4px;
}

input,
textarea,
select {
  background-color: #fff;
}
input:focus,
textarea:focus,
select:focus {
  border-color: #ccc;
}

input,
button {
  overflow: visible; /* IE6 fix */
}

input[type=checkbox], input[type=radio] {
  margin: auto 5px;
  width: auto;
  vertical-align: middle;
}

input[type=submit],
input[type=button],
input[type=reset],
button {
  width: auto;
  height: auto;
  cursor: pointer;
  border: 0;
  padding: 7px 10px 8px;
  font-weight: bold;
  font-size: 13px;
  line-height: 100%;
  text-align: center;
  background-color: #ddd;
}
input[type=submit]:focus,
input[type=button]:focus,
input[type=reset]:focus,
button:focus {
  border-width: 0;
}

.opera:-o-prefocus,
input[type=submit],
input[type=button],
input[type=reset],
button {
  border-right: 1px solid rgba(0, 0, 0, 0);
}

label {
  cursor: pointer;
  font-size: 13px;
}
label[for] {
  width: auto;
}
label > * {
  font-size: 14px;
}

/** Search form */
.form.search {
  position: relative;
}
.form.search input[type=search] {
  margin: 0;
  padding: 6px 8px;
}
.form.search button {
  position: absolute;
  top: 2px;
  right: 2px;
  margin-bottom: 0;
  padding: 7px 10px;
  border: 0;
  font-size: 12px;
  z-index: 1;
}

/**
 * Table
