#cancel-comment-reply-link {
  line-height: 160%;
  font-size: 13px;
}

.comment-meta {
  color: #999;
}
.comment-meta::after {
  content: "";
  display: table;
  clear: both;
}
.comment-meta .edit-link::before {
  content: "| ";
}

#cancel-comment-reply-link {
  text-align: right;
  font-weight: normal;
}

#reply-title small {
  margin-left: 20px;
}

.comment-form .form-submit {
  margin-bottom: 0;
}

.comment-notes,
.logged-in-as {
  color: #999;
  font-size: 13px;
}

.required {
  color: #f00;
}

.comment-content::after {
  content: "";
  display: table;
  clear: both;
}

.comments-closed-text {
  margin-top: 20px;
  margin-bottom: 20px;
  color: #999;
}
.comments-closed-text:first-child {
  margin-top: 0;
}
.comments-closed-text:last-child {
  margin-bottom: 0;
}